}
.welcome > * {
    display: block;
    width: 100%;
    position: relative;
}
.heading {
  font-size: 80px;
  margin: 10% 4rem 0 0;
  text-align: right;
  -webkit-transition: color 0.4s;
  transition: color 0.4s;
  color: var(--accent);
