.nav {
  background-color: var(--nav-bg, transparent);
  width: 100%;
  position: fixed;
  left: 0;
  top: 20px;
  z-index: 100;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.nav-circle {
  border: 2px solid var(--accent);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: all 0.4s, border-color 1.8s;
  transition: all 0.4s, border-color 1.8s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform-origin: center;
          transform-origin: center;
  text-align: center;
  line-height: 40px;
  color: var(--accent);
  font-size: 20px;
}
.nav-circle span {
    display: inline-block;
}
.nav-circle svg {
    margin: 6px;
}
.nav-circle:hover {
    -webkit-box-shadow: 0 0 2px var(--accent), 0 0 3px var(--text-color), 0 0 2px var(--accent) inset;
            box-shadow: 0 0 2px var(--accent), 0 0 3px var(--text-color), 0 0 2px var(--accent) inset;
}
.nav-circle:active {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}
.nav-circle-appear {
    -webkit-transform: scale(0.2);
            transform: scale(0.2);
    opacity: 0;
}
@-webkit-keyframes scale {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
30% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes scale {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
30% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@media screen and (max-width: 600px) {
